.home-section {
  position: relative;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.particles-canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.home-content {
  position: relative;
  z-index: 2;
  text-align: center;
  max-width: 900px;
  width: 100%;
}

.hero-image {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 1.5rem;
  border: 4px solid #fff;
  animation: float 6s ease-in-out infinite;
}

.second-image {
  width: 300px;
  margin: 2rem auto 0 auto;
  display: block; 
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.2);
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-15px); }
  100% { transform: translateY(0px); }
}

.home-title {
  font-size: 2.8rem;
  color: #fff;
  margin-bottom: 1rem;
}

.home-subtitle {
  font-size: 1.6rem;
  color: #ffdf00; 
  margin-bottom: 1.5rem;
}

.home-paragraph {
  font-size: 1.1rem;
  color: #f9f9f9;
  line-height: 1.6;
  margin-bottom: 2rem;
}

.cta-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.cta-button {
  display: inline-block;
  padding: 0.8rem 2rem;
  background-color: #ffd700; 
  color: #333;
  text-decoration: none;
  border-radius: 25px;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.secondary-cta {
  background-color: #333;
  color: #fff;
}

.cta-button:hover {
  background-color: #ffe256;
  transform: translateY(-2px);
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 2rem;
}

.social-icons a {
  color: #fff;
  font-size: 1.8rem;
  transition: color 0.3s ease, transform 0.3s ease;
}

.social-icons a:hover {
  color: #ffd700;
  transform: scale(1.2);
}

.scroll-arrow {
  margin-top: 2rem;
  color: #fff;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-10px); }
  60% { transform: translateY(-5px); }
}

@media (max-width: 768px) {
  .home-title {
    font-size: 2rem;
  }

  .home-subtitle {
    font-size: 1.2rem;
  }

  .home-paragraph {
    font-size: 1rem;
  }

  .cta-button {
    padding: 0.6rem 1.5rem;
  }

  .social-icons a {
    font-size: 1.5rem;
  }

  .hero-image {
    width: 150px;
    height: 150px;
  }

  .second-image {
    width: 250px;
  }
}

@media (max-width: 480px) {
  .home-title {
    font-size: 1.8rem;
  }

  .home-subtitle {
    font-size: 1rem;
  }

  .home-paragraph {
    font-size: 0.95rem;
  }

  .cta-button {
    padding: 0.5rem 1.2rem;
    font-size: 0.9rem;
  }

  .social-icons a {
    font-size: 1.3rem;
  }

  .hero-image {
    width: 100px;
    height: 100px;
  }

  .second-image {
    width: 200px;
  }
}
