.cursor {
    position: fixed;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    background-color: rgba(255, 215, 0, 0.7);
    border-radius: 50%;
    pointer-events: none;
    transform: translate(-50%, -50%);
    transition: transform 150ms ease, background-color 150ms ease;
    z-index: 10000;
  }
  
  a:hover + .cursor,
  button:hover + .cursor {
    transform: translate(-50%, -50%) scale(1.5);
    background-color: rgba(255, 215, 0, 1);
  }
  