.header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 60px;
    background-color: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(8px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2rem;
    z-index: 999;
  }
  
  .logo {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #fff;
    font-weight: 600;
    font-size: 1.2rem;
  }
  
  .logo-icon {
    font-size: 1.5rem;
  }
  
  .nav a {
    margin-left: 1rem;
    text-decoration: none;
    color: #fff;
    font-weight: 500;
    transition: color 0.3s ease;
  }
  
  .nav a:hover {
    color: #ffd700;
  }
  
  @media screen and (max-width: 768px) {
    .header {
      flex-direction: column;
      height: auto;
      padding: 0.5rem;
    }
  
    .logo {
      margin-bottom: 0.5rem;
    }
  
    .nav {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .nav a {
      margin: 0.5rem 0;
    }
  }