.about-section {
    min-height: 100vh;
    padding: 4rem 2rem;
    background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    text-align: center;
  }
  
  .about-container {
    max-width: 1000px;
    width: 100%;
  }
  
  .about-title {
    font-size: 3rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
    color: #ffd700; 
    text-transform: uppercase;
  }
  
  .profile-photo {
    margin-bottom: 1.5rem;
  }
  .profile-photo img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    object-fit: cover;
    border: 4px solid #ffd700;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  .profile-photo img:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 15px rgba(255, 215, 0, 0.5);
  }
  
  .featured-quote {
    font-style: italic;
    font-size: 1.2rem;
    margin: 1rem auto 2rem;
    max-width: 600px;
    line-height: 1.4;
    position: relative;
    color: #fefefe;
  }
  .featured-quote .quote-author {
    display: block;
    margin-top: 0.5rem;
    font-size: 1rem;
    color: #ffd700;
  }
  
  .education,
  .additional-info,
  .timeline,
  .hobbies,
  .skills-section {
    background: rgba(0, 0, 0, 0.3);
    padding: 1.5rem;
    border-radius: 8px;
    margin-bottom: 2rem;
    text-align: left; 
  }
  .education h3,
  .additional-info h3,
  .timeline h3,
  .hobbies h3,
  .skills-section h3 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: #ffd700;
    display: flex;
    align-items: center;
  }
  .section-icon {
    margin-right: 0.5rem;
  }
  
  .education p,
  .additional-info p {
    font-size: 1.1rem;
    line-height: 1.6;
    color: #ddd;
  }
  
  .skills-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
  }
  .skill-item {
    flex: 1 1 300px;
    margin-bottom: 1rem;
  }
  .skill-title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.5rem;
    font-size: 1rem;
    color: #fff;
  }
  .skill-level {
    font-weight: bold;
  }
  .skill-bar {
    width: 100%;
    height: 10px;
    background: #ddd;
    border-radius: 5px;
    overflow: hidden;
  }
  .skill-progress {
    height: 100%;
    background-color: #ffd700;
  }
  
  .timeline ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  .timeline-item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 2rem;
    position: relative;
  }
  .timeline-icon {
    font-size: 2rem;
    color: #ffd700;
    margin-right: 1rem;
    flex-shrink: 0;
  }
  .timeline-content h4 {
    font-size: 1.2rem;
    color: #fff;
    margin: 0;
  }
  .timeline-content h5 {
    font-size: 1rem;
    color: #ffd700;
    margin: 0.3rem 0;
  }
  .timeline-content p {
    font-size: 0.95rem;
    color: #ddd;
    margin: 0;
  }
  
  .additional-info p {
    font-size: 1.1rem;
    color: #ddd;
  }
  
  .hobbies-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 1.5rem;
    margin-top: 1rem;
  }
  .hobby-item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .hobby-icon {
    font-size: 2.5rem;
    color: #ffd700;
    margin-bottom: 0.5rem;
    transition: transform 0.3s ease, color 0.3s ease;
  }
  .hobby-item p {
    font-size: 1rem;
    color: #ddd;
  }
  .hobby-item:hover .hobby-icon {
    transform: scale(1.2);
    color: #fff;
  }
  
  .download-resume {
    display: inline-block;
    margin-top: 1.5rem;
    padding: 0.8rem 1.5rem;
    background-color: #ffd700;
    color: #333;
    text-decoration: none;
    border-radius: 4px;
    font-weight: 600;
    transition: background 0.3s ease, transform 0.3s ease;
    text-align: center;
  }
  .download-resume:hover {
    background-color: #ffe256;
    transform: translateY(-3px);
  }
  
  @media (max-width: 768px) {
    .about-title {
      font-size: 2.5rem;
    }
    .education h3,
    .additional-info h3,
    .timeline h3,
    .hobbies h3,
    .skills-section h3 {
      font-size: 1.5rem;
    }
    .education p,
    .additional-info p,
    .timeline-content p,
    .hobbies p,
    .skill-title {
      font-size: 1rem;
    }
    .timeline-icon {
      font-size: 1.8rem;
    }
    .hobby-icon {
      font-size: 2rem;
    }
    .profile-photo img {
      width: 150px;
      height: 150px;
    }
  }
  
  @media (max-width: 480px) {
    .about-title {
      font-size: 2rem;
    }
    .education h3,
    .additional-info h3,
    .timeline h3,
    .hobbies h3,
    .skills-section h3 {
      font-size: 1.3rem;
    }
    .timeline-icon {
      font-size: 1.5rem;
    }
    .hobby-icon {
      font-size: 1.8rem;
    }
    .profile-photo img {
      width: 120px;
      height: 120px;
    }
  }
  