.projects-section {
  padding: 4rem 2rem;
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  color: #fff;
  text-align: center;
}

.projects-container {
  max-width: 1200px;
  margin: 0 auto;
}

.section-title {
  font-size: 2.8rem;
  margin-bottom: 0.5rem;
  color: #ffd700; 
  text-transform: uppercase;
}

.section-subtitle {
  font-size: 1.2rem;
  margin-bottom: 3rem;
  color: #fefefe;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 2rem;
}

.project-card {
  background: transparent;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
}

.project-image-container {
  position: relative;
  width: 100%;
  border-radius: 8px;
  overflow: hidden;
}

.project-image {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 8px;
  transition: transform 0.3s ease;
}

.project-card:hover .project-image {
  transform: scale(1.08);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.65);
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.project-card:hover .overlay {
  opacity: 1;
}

.overlay-title {
  font-size: 1.4rem;
  margin-bottom: 0.8rem;
  color: #ffd700; 
  font-weight: bold;
}

.overlay-description {
  font-size: 0.95rem;
  line-height: 1.4;
  color: #f0f0f0;
  margin: 0 0 1rem;
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.tech-chip {
  background: #ffd700;
  color: #333;
  font-size: 0.8rem;
  padding: 0.3rem 0.6rem;
  border-radius: 15px;
}

.project-link {
  display: inline-block;
  text-decoration: none;
  background-color: #ffd700;
  color: #333;
  padding: 0.6rem 1.2rem;
  border-radius: 20px;
  font-weight: 600;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.project-link:hover {
  background-color: #ffe256;
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .section-title {
    font-size: 2.2rem;
  }

  .project-image {
    height: auto;
  }

  .overlay-description {
    font-size: 0.9rem;
  }

  .tech-chip {
    font-size: 0.75rem;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 1.8rem;
  }

  .overlay-title {
    font-size: 1.2rem;
  }

  .overlay-description {
    font-size: 0.85rem;
  }

  .tech-chip {
    font-size: 0.7rem;
  }
}
