.footer-section {
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  position: relative;
  color: #fff;
  text-align: center;
}

.wave-divider {
  position: relative;
  width: 100%;
  height: 80px;
  overflow: hidden;
  margin-bottom: -5px; 
}
.wave-divider svg {
  display: block;
  width: 100%;
  height: 100%;
}

.footer-content {
  padding: 4rem 0; 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-cta {
  font-size: 1rem;
  margin-bottom: 1rem;
  max-width: 700px;
  line-height: 1.4;
  color: #fdfdfd;
  padding: 0 1rem;
}

.footer-heading {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: #ffd700; 
  text-transform: uppercase;
}

.social-section {
  display: flex;
  gap: 3rem; 
  margin-bottom: 2rem;
}

.social-icon {
  font-size: 3rem; 
  color: #fff;
  transition: color 0.3s, transform 0.3s;
}
.social-icon:hover {
  color: #ffd700; 
  transform: scale(1.2);
}

.footer-text {
  font-size: 1rem;
  margin-top: 1rem;
  color: #fdfdfd;
}

@media (max-width: 768px) {
  .footer-heading {
    font-size: 2rem;
    margin-bottom: 1.5rem;
  }
  .social-icon {
    font-size: 2.4rem;
  }
  .social-section {
    gap: 2rem;
  }
  .footer-cta {
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .footer-heading {
    font-size: 1.6rem;
  }
  .social-icon {
    font-size: 2rem;
  }
  .footer-cta {
    font-size: 0.9rem;
  }
}
