.contact-section {
  position: relative;
  width: 100%;
  padding: 4rem 2rem;
  background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
  color: #fff;
  text-align: center;
  margin-top: 60px; 
}

.contact-container {
  max-width: 700px;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.3); 
  backdrop-filter: blur(6px);
  border-radius: 8px;
  padding: 2rem;
}

.contact-title {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  color: #ffd700; 
  text-transform: uppercase;
}

.contact-subtitle {
  font-size: 1.1rem;
  margin-bottom: 2rem;
  color: #fefefe;
}

.contact-info {
  margin-bottom: 2rem;
}

.contact-email {
  font-size: 1rem;
  color: #ffd700;
  text-decoration: none;
  transition: color 0.3s ease;
}
.contact-email:hover {
  color: #fff;
}

.contact-socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 0.5rem;
}

.contact-socials a {
  color: #ffd700;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease, transform 0.3s ease;
}
.contact-socials a:hover {
  color: #fff;
  transform: scale(1.1);
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  margin-top: 2rem;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 0.9rem;
  border-radius: 4px;
  border: none;
  font-size: 1rem;
  font-family: inherit;
  outline: none;
  color: #333;
}

.contact-form ::placeholder {
  color: #999;
}

.contact-form button {
  padding: 0.9rem;
  border: none;
  background: #ffd700;
  color: #333;
  font-weight: 600;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease;
}

.contact-form button:hover {
  background: #ffe256;
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .contact-title {
    font-size: 2rem;
  }
  .contact-subtitle {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .contact-title {
    font-size: 1.8rem;
  }
}
