.skills-section {
    min-height: 100vh;
    padding: 4rem 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 60px; 
  
    background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
    color: #fff;
    text-align: center;
  }
  
  .skills-container {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .skills-title {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    color: #ffd700; 
    text-transform: uppercase;
  }
  
  .skills-description {
    font-size: 1.2rem;
    margin-bottom: 3rem;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    line-height: 1.6;
    color: #fefefe;
  }
  
  .skills-category {
    margin-bottom: 3rem;
  }
  
  .category-title {
    font-size: 2rem;
    margin-bottom: 2rem;
    color: #fff;
    text-decoration: underline;
    text-underline-offset: 6px;
  }
  
  .skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 2rem;
  }
  
  .flip-card {
    perspective: 1000px; 
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 240px;
    transition: transform 0.6s;
    transform-style: preserve-3d;
  }
  
  .flip-card-front,
  .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    backface-visibility: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .flip-card-front {
    padding: 1rem;
    transition: transform 0.3s ease;
  }
  
  .flip-card-back {
    padding: 1rem;
    transform: rotateY(180deg);
    text-align: center;
  }
  
  .flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
  }
  
  .skill-icon {
    font-size: 2.5rem;
    margin-bottom: 0.8rem;
    color: #ffd700;
  }
  
  .skill-name {
    font-size: 1.3rem;
    font-weight: 600;
  }
  
  .skill-details {
    font-size: 0.95rem;
    margin: 0 0.5rem;
    color: #f0f0f0;
    line-height: 1.4;
  }
  
  .flip-card-front:hover,
  .flip-card-back:hover {
    background: rgba(0, 0, 0, 0.3);
  }
  
  @media (max-width: 768px) {
    .skills-title {
      font-size: 2rem;
    }
    .skills-description {
      font-size: 1rem;
    }
    .category-title {
      font-size: 1.5rem;
    }
    .flip-card-inner {
      height: 220px;
    }
  }
  
  @media (max-width: 480px) {
    .skills-title {
      font-size: 1.8rem;
    }
    .category-title {
      font-size: 1.3rem;
    }
    .flip-card-inner {
      height: 200px;
    }
  }
  